import React from 'react';

interface IFlexProps {
  f: number;
  alignItems?: 'flex-start' | 'center' | 'flex-end';
  justifyContent?: 'flex-start' | 'center' | 'flex-end';
  direction?: 'row' | 'column';
  hasMargin?: boolean;
}

export const Flex: React.FC<IFlexProps> = ({
  f: flex,
  alignItems = 'flex-start',
  justifyContent = 'flex-start',
  direction = 'row',
  hasMargin = false,
  ...props
}) => {
  const marginStyle = hasMargin ? { margin: '0 10pt' } : {};

  return <div
    {...props}
    style={{
      display: 'flex',
      flex,
      alignItems,
      justifyContent,
      flexDirection: direction,
      ...marginStyle,
    }}
  />;
}

interface IFlexContainerProps {
  hasMargin?: boolean;
}

export const FlexContainer: React.FC<IFlexContainerProps> = ({
  hasMargin = false,
  ...props
}) => {
  const marginStyle = hasMargin ? { margin: '0 -10pt' } : {};

  return <div {...props} style={{ display: 'flex', width: '100%', ...marginStyle }} />;
}
