import React from 'react';

import './double-list.sass';

interface IDoubleListProps {
  left: string[];
  right: string[];
}

export const DoubleList: React.FC<IDoubleListProps> = (props) => {
  const keys = ['left', 'right'] as const;

  return (
    <div className="double-list">
      {
        keys.map((list: 'left' | 'right') => (
          <ul key={list}>
            {
              props[list].map(item => (
                <li key={item}>{item}</li>
              ))
            }
          </ul>
        ))
      }
    </div>
  );
}
