import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';

import './button.sass';

interface IButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  alternative?: boolean;
  link?: string;
}

export const Button: React.FC<IButtonProps> = ({ alternative, link, ...defaultProps }) => {
  const className = classnames(defaultProps.className, 'btn', { 'btn--alternative': alternative });
  const Component: any = link ? Link : 'button';
  const props = link ? { ...defaultProps, to: link } : defaultProps;

  return (
    <Component {...props} className={className} />
  );
}
