import React, { useEffect } from 'react';
import { Switch, Route, useLocation, useHistory } from 'react-router-dom';

import { Button } from './components/Button';
import { Menu } from './components/Menu';
import { Logo } from './components/Logo';
import { Topbar } from './components/Topbar';
import { Flex } from './components/Flex';
import { Footer } from './components/Footer';
import { MainContainer } from './components/MainContainer';

import { Home } from './pages/home';
import { About } from './pages/about';
import { Portfolio } from './pages/portfolio';
/* personal */
import { MyLogo } from './pages/portfolio/personal/mylogo';
import { Animes } from './pages/portfolio/personal/animes';
import { CVIronMan } from './pages/portfolio/personal/cvironman';
import { Mozzie } from './pages/portfolio/personal/mozzie';
import { Thanatos } from './pages/portfolio/personal/thanatos';
import { TravelLeisure } from './pages/portfolio/personal/travelleisure';
import { Utopia } from './pages/portfolio/personal/utopia';
import { Disney } from './pages/portfolio/personal/disney';
/* professional */
import { Caporl } from './pages/portfolio/professional/caporl';
import { BlueanchorVisitCards } from './pages/portfolio/professional/blueanchor/visitcards';
import { BlueanchorWishesCard } from './pages/portfolio/professional/blueanchor/wishescards';
import { BlueanchorFlyers } from './pages/portfolio/professional/blueanchor/flyers';
import { BlueanchorKakemono } from './pages/portfolio/professional/blueanchor/kakemono';
import { BlueanchorKakemonoV2 } from './pages/portfolio/professional/blueanchor/kakemonov2';
import { BluecampLogo } from './pages/portfolio/professional/bluecamp/logo';
import { BlueschoolLogo } from './pages/portfolio/professional/blueschool/logo';
import { BlueschoolWireframes } from './pages/portfolio/professional/blueschool/wireframes';
import { BlueschoolMascot } from './pages/portfolio/professional/blueschool/mascot';

const App: React.FC = () => {
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    const params = new URLSearchParams(location.search);

    if (params.has('url')) {
      history.replace(params.get('url') || '/');
    }
  }, []);

  return (
    <div>
      <Topbar>
        <Flex f={1}><Logo /></Flex>
        <Menu />
        <Flex f={1} justifyContent="flex-end" >
          <Button onClick={() => window.open('/cv-melody-sinbandhit.pdf')}>Télécharger mon CV</Button>
        </Flex>
      </Topbar>
      <MainContainer>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/about-me" component={About} />
          <Route exact path="/portfolio/my-logo" component={MyLogo} />
          <Route exact path="/portfolio/appli-animes" component={Animes} />
          <Route exact path="/portfolio/cv-iron-man" component={CVIronMan} />
          <Route exact path="/portfolio/mozzie" component={Mozzie} />
          {/* <Route exact path="/portfolio/emp" component={EMP} /> */}
          <Route exact path="/portfolio/thanatos" component={Thanatos} />
          <Route exact path="/portfolio/travel-leisure" component={TravelLeisure} />
          <Route exact path="/portfolio/utopia" component={Utopia} />
          <Route exact path="/portfolio/disney" component={Disney} />
          <Route exact path="/portfolio/caporl" component={Caporl} />
          <Route exact path="/portfolio/blueanchor/visit-cards" component={BlueanchorVisitCards} />
          <Route exact path="/portfolio/blueanchor/wishes-cards" component={BlueanchorWishesCard} />
          <Route exact path="/portfolio/blueanchor/flyers" component={BlueanchorFlyers} />
          <Route exact path="/portfolio/blueanchor/kakemono" component={BlueanchorKakemono} />
          <Route exact path="/portfolio/blueanchor/kakemono-v2" component={BlueanchorKakemonoV2} />
          <Route exact path="/portfolio/bluecamp/logo" component={BluecampLogo} />
          <Route exact path="/portfolio/blueschool/logo" component={BlueschoolLogo} />
          <Route exact path="/portfolio/blueschool/wireframes" component={BlueschoolWireframes} />
          <Route exact path="/portfolio/blueschool/mascot" component={BlueschoolMascot} />
          <Route exact path="/portfolio" component={Portfolio} />
        </Switch>
      </MainContainer>
      <Footer />
    </div>
  );
}

export default App;
