import React from 'react';

import { Button } from '../Button';

import './show-card.sass';

interface IShowCardProps {
  title: string;
  link: string;
  imageLink: string;
}

export const ShowList: React.FC = ({ children, ...props }) => (
  <div {...props} className="show-list">
    <div className="show-list--container">
      {children}
    </div>
  </div>
);

export const ShowCard: React.FC<IShowCardProps> = (props) => {
  return (
    <div className="show-card">
      <div className="show-card--cover" style={{ backgroundImage: `url('${props.imageLink}')` }} />
      <div className="show-card--title">
        {props.title}
      </div>
      <div className="show-card--blank" />
      <Button className="show-card--button" link={props.link}>Regarder</Button>
    </div>
  );
}
