import React, { useState, useEffect } from 'react';
import classnames from 'classnames';

import './container.sass';
import { Button } from '../Button';

interface IContainerProps {
  cover?: string;
  tabs?: boolean;
  largePadding?: boolean;
}

export const Container: React.FC<IContainerProps> = (props) => {
  const { children, cover, tabs, largePadding } = props;
  const [tab, setTab] = useState<string>();

  useEffect(() => {
    const keys = React.Children.map(children, (child: any) => child.key);

    if (keys && keys.length) {
      setTab(keys[0])
    }
  }, []);

  return (
    <div className={classnames(
      'container',
      {
        '--coverered': cover,
        '--tabs': tabs,
        '--largePadding': largePadding,
      }
    )}>
      {
        tabs
          ? (
            <div className="container--tabs">
              {
                React.Children.map(children, (child: any) => (
                  child && child.key
                    ? (
                      <Button
                        key={child.key}
                        onClick={() => setTab(child.key)}
                        className={child.key === tab ? '--active' : undefined}
                        alternative
                      >
                        {child.key}
                      </Button>
                    )
                    : null
                ))
              }
            </div>
          )
          : null
      }
      {
        cover
          ? (
            <div className="container--cover" style={{ backgroundImage: `url('${cover}')` }} />
          )
          : null
      }
      {
        tabs
          ? React.Children.map(children, (child: any) => child && child.key === tab ? child : null)
          : children
      }
    </div>
  );
}
