import React from 'react';
import { NavLink, NavLinkProps } from 'react-router-dom';

import './menu.sass';

export const MenuLink: React.FC<NavLinkProps> = (props) => {
  return (
    <NavLink {...props} className="menu--link">
      {props.children}
    </NavLink>
  )
}

export const Menu: React.FC = () => {
  return (
    <div className="menu">
      <MenuLink to="/about-me">À propos de moi</MenuLink>
      <MenuLink to="/portfolio">Portfolio</MenuLink>
    </div>
  );
}
