import React from 'react';
import classnames from 'classnames';

import './paragraph.sass';

interface IParagraphProps {
  fullWidth?: boolean;
}

export const Paragraph: React.FC<IParagraphProps> = ({ fullWidth = false, ...props }) => {
  const className = classnames('paragraph', { 'paragraph--full-width': fullWidth });

  return (
    <p className={className}>
      {props.children}
    </p>
  );
}
